"use client";

import { useEffect, useState } from "react";
import {
  getContents,
  deleteContent,
  togglePublish,
} from "@/services/content.service";
import CreateContentForm from "@/components/admin/CreateContentForm";

export default function AdminContents() {
  const [contents, setContents] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [showCreate, setShowCreate] = useState(false);

  const fetchContents = async () => {
    setLoading(true);
    try {
      const res = await getContents();
      setContents(res.data); // ContentResource::collection
    } catch (e) {
      console.error("Fetch contents error", e);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchContents();
  }, []);

  const handleDelete = async (uuid: string) => {
    if (!confirm("ŸáŸÑ ÿ£ŸÜÿ™ ŸÖÿ™ÿ£ŸÉÿØ ŸÖŸÜ ÿßŸÑÿ≠ÿ∞ŸÅÿü")) return;
    await deleteContent(uuid);
    fetchContents();
  };

  const handlePublish = async (uuid: string) => {
    await togglePublish(uuid);
    fetchContents();
  };

  if (loading) {
    return <p className="text-gray-400">ÿ¨ÿßÿ±Ÿç ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖÿ≠ÿ™ŸàŸâ...</p>;
  }

  return (
    <div>
      <h2 className="text-2xl font-bold mb-6 text-right">ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖÿ≠ÿ™ŸàŸâ</h2>

      {contents.length === 0 && <p className="text-gray-400">ŸÑÿß ŸäŸàÿ¨ÿØ ŸÖÿ≠ÿ™ŸàŸâ</p>}
      <div className="flex justify-end mb-6">
        <button
          onClick={() => setShowCreate(!showCreate)}
          className="bg-green-600 hover:bg-green-700 px-4 py-2 rounded font-semibold transition"
        >
          ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≠ÿ™ŸàŸâ
        </button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {contents.map((item) => (
          <div
            key={item.uuid}
            className="bg-gray-800 rounded-lg overflow-hidden shadow hover:shadow-lg transition"
          >
            {/* ÿµŸàÿ±ÿ© */}
            <div
              className="h-48 bg-cover bg-center"
              style={{
                backgroundImage: `url(${item.card_image || item.poster_image})`,
              }}
            />

            {/* ÿ®ŸäÿßŸÜÿßÿ™ */}
            <div className="p-4 text-right">
              <h3 className="font-bold text-lg mb-1">{item.title}</h3>

              <p className="text-gray-400 text-sm mb-2">
                {item.type} ‚Ä¢ {item.year || "‚Äî"}
              </p>

              <p className="text-gray-300 text-sm line-clamp-2 mb-4">
                {item.description}
              </p>

              {/* ÿ£ÿ≤ÿ±ÿßÿ± */}
              <div className="flex flex-wrap gap-2 justify-end">
                <button
                  onClick={() => handlePublish(item.uuid)}
                  className="px-3 py-1 text-sm rounded bg-blue-600 hover:bg-blue-700 transition"
                >
                  ŸÜÿ¥ÿ± / ÿ•ŸÑÿ∫ÿßÿ°
                </button>

                <button
                  onClick={() => handleDelete(item.uuid)}
                  className="px-3 py-1 text-sm rounded bg-red-600 hover:bg-red-700 transition"
                >
                  ÿ≠ÿ∞ŸÅ
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
